<template>
  <div class="logistics">
    <back-header title="物流信息" />
    <Scroll ref="scroll" @pullingDown="pullingDown" clickable :pull-down-refresh="{threshold: 70, stop: 56}">
      <div class="order">
        <img :src="cover" alt="">
        <div class="info">
          <h4>{{ order.express_company }}</h4>
          <p><span>快递单号：</span>{{ order.express_no }}</p>
          <p v-if="false"><span>官方客服：</span>326-665565</p>
        </div>
      </div>
      <div class="time-line">
        <div :class="['desc-item', index === 0 ? 'active' : '']" v-for="(item, index) in timeLine" :key="item.id">
          <p>{{ item.context }}</p>
          <span>{{ item.time }}</span>
          <i class="circle"/>
          <i v-show="index < timeLine.length - 1" class="vertical"/>
        </div>
      </div>
    </Scroll>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import { post } from '../../common/js/http'

export default {
  name: 'logistics',
  components: {
    BackHeader,
    Scroll
  },
  data() {
    return {
      timeLine: [],
      count: 0,
      isPulling: false,
      cover: '',
      order: {}
    }
  },
  created() {
    this.cover = this.$route.params.cover
    this.order = this.$route.params.order
    this.getList()
  },
  methods: {
    getList() {
      post('/shop/order/getExpressInfo', {
        suborderid: this.order.id
      })
        .then(({ data: { data } }) => {
          this.timeLine = data.data
        })
    },
    refresh() {
      setTimeout(() => {
        this.isPulling = false
      }, 1000)
    },
    pullingDown() {
      setTimeout(() => {
        this.$refs.scroll.closeRefresh()
      }, 2000)
    }
  }
}
</script>

